import React from "react";
import { Outlet, useLocation, useNavigate } from "react-router";
import styled from "styled-components";

interface SettingProps {
  isActive: boolean;
}

const StyledUserPageWrapper = styled.div`
  flex-wrap: wrap;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
`;

const StyledSettingWrapper = styled.div`
  display: flex;
  width: calc(100% - 4em);
  height: 5.125em;
  margin: 1em 2em 0 2em;
  flex-direction: column;
  border-bottom: 1px solid var(--color--search--background);
  justify-content: space-between;
`;

const StyledTabWrapper = styled.ul`
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style: none;
`;

const StyledSettingDiv = styled.div`
  margin: 1em 2em;
  height: calc(100vh - 11.8em);
  width: calc(100% - 4em);
`;

const StyledTabItem = styled.li<SettingProps>`
  border-bottom: 1px solid
    ${({ isActive }) => (isActive ? "var(--color--main)" : "transparent")};
  margin-right: 1.5em;
`;

const StyledTabLink = styled.a<SettingProps>`
  user-select: none;
  line-height: 2em;
  color: ${({ isActive }) =>
    isActive ? "var(--color--main)" : "var(--color--white)"};
  text-decoration: none;
`;

const settingTabItems = [
  { tab: "", href: `/user/profile` },
  { tab: " ", href: "/user/history" },
];

const User = () => {
  const location = useLocation();
  const navigate = useNavigate();

  return (
    <StyledUserPageWrapper>
      <StyledSettingWrapper>
        <span
          style={{
            lineHeight: "1.5625em",
            fontSize: "2em",
            fontWeight: "bold",
          }}
        >
          
        </span>
        <StyledTabWrapper>
          {settingTabItems.map((item) => {
            const isActive = item.href === location.pathname;
            return (
              <StyledTabItem isActive={isActive} key={item.tab}>
                <StyledTabLink
                  onClick={() => {
                    navigate("/react-ts/#" + item.href);
                    window.location.reload();
                  }}
                  isActive={isActive}
                  href={"/react-ts/#" + item.href}
                >
                  {item.tab}
                </StyledTabLink>
              </StyledTabItem>
            );
          })}
        </StyledTabWrapper>
      </StyledSettingWrapper>
      <StyledSettingDiv>
        <Outlet></Outlet>
      </StyledSettingDiv>
    </StyledUserPageWrapper>
  );
};

export default User;
