import React, { useRef } from "react";
import styled from "styled-components";

const StyledProfileWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

const StyledUidWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-direction: column;
  border-radius: var(--border--radius);
  box-sizing: border-box;
  border: 1px solid var(--color--header--background--hover);
  margin: 1em 0;
  width: 100%;
  max-width: 40em;
  background-color: var(--color--header);
`;

const StyledMainTitle = styled.span`
  font-size: 1.25em;
`;
const StyledSubTitle = styled.span`
  font-size: 0.85em;
  color: var(--color--division);
`;

const StyledSettingInput = styled.input`
  width: calc(100% - 1em - 41.86px);
  margin: 1em 0 1em 1em;
  background-color: var(--color--header--background);
  color: var(--color--white);
  border: none;
  height: 1.75em;
  padding: 0;
`;

const StyledSettingSaveButton = styled.button`
  font-family: "KoPubWorldDotum";
  color: var(--color--white);
  cursor: pointer;
  margin: 1em;
  width: 6em;
  height: 2em;
  font-weight: bold;
  background-color: var(--color--main);
  border-radius: var(--border--radius);
  :hover {
    background-color: var(--color--main--hover);
  }
`;

const Profile = () => {
  const uid = localStorage.getItem("uid") || "null";
  let inputRef = useRef<HTMLInputElement>(null);

  function onClickSave() {
    const input = inputRef.current;
    if (!input) {
      alert(" ");
      return;
    }

    localStorage.setItem("uid", input.value);
    alert(" ");
    window.location.reload();
  }

  return (
    <StyledProfileWrapper>
      <StyledMainTitle>  </StyledMainTitle>
      <StyledSubTitle>    .</StyledSubTitle>
      <StyledUidWrapper>
        <div
          style={{
            width: "calc(100% - 2em)",
            margin: "1em",
          }}
        >
          <span></span>
          <StyledSettingInput
            ref={inputRef}
            defaultValue={uid}
          ></StyledSettingInput>
        </div>
        <StyledSettingSaveButton onClick={onClickSave}>
          
        </StyledSettingSaveButton>
      </StyledUidWrapper>
    </StyledProfileWrapper>
  );
};

export default Profile;
