import React, { useState } from "react";
import styled from "styled-components";
import usePlaylist, { Playlist } from "../hooks/usePlaylist";
import useSearchHistory from "../hooks/useSearchHistory";
import UserInfoItem from "./UserInfoItem";

const StyledProfileWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

const StyledUidWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  border-radius: var(--border--radius);
  box-sizing: border-box;
  border: 1px solid var(--color--header--background--hover);
  margin: 1em 0;
  padding: 0.5em;
  width: 100%;
  max-width: 40em;
  background-color: var(--color--header);
`;

const StyledMainTitle = styled.span`
  font-size: 1.25em;
`;
const StyledSubTitle = styled.span`
  font-size: 0.85em;
  color: var(--color--division);
`;

const History = () => {
  const [, removePlaylist] = usePlaylist();
  const [playlist, setPlaylist] = useState<Array<Playlist>>(
    JSON.parse(localStorage.getItem("playlist") as string)
  );
  const [, removeHistory] = useSearchHistory();
  const [history, setHistory] = useState<Array<string>>(
    JSON.parse(localStorage.getItem("searchHistory") as string)
  );

  return (
    <StyledProfileWrapper>
      <StyledMainTitle> </StyledMainTitle>
      <StyledSubTitle>   .</StyledSubTitle>
      <StyledUidWrapper>
        <span style={{ margin: "0.5em", fontWeight: "bold" }}> </span>
        {playlist &&
          playlist.map((item) => {
            return (
              <UserInfoItem
                removeItem={() => {
                  removePlaylist(item.videoId);
                  setPlaylist(
                    JSON.parse(localStorage.getItem("playlist") as string)
                  );
                }}
                key={item.videoId}
                item={item.title}
              />
            );
          })}
        {playlist.length === 1 && (
          <span
            style={{
              display: "block",
              margin: "0.5em",
            }}
          >
              .
          </span>
        )}
      </StyledUidWrapper>
      <StyledUidWrapper>
        <span style={{ margin: "0.5em", fontWeight: "bold" }}> </span>
        {history &&
          history.map((item: string) => {
            return (
              <UserInfoItem
                removeItem={() => {
                  removeHistory(item);
                  setHistory(
                    JSON.parse(localStorage.getItem("searchHistory") as string)
                  );
                }}
                key={item}
                item={item}
              />
            );
          })}
        {!history && (
          <span
            style={{
              display: "block",
              margin: "0.5em",
            }}
          >
               .
          </span>
        )}
      </StyledUidWrapper>
    </StyledProfileWrapper>
  );
};

export default History;
